body {
  background-color: black;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

nav {
    z-index: 1000;
    font-family: 'Ubuntu', sans-serif;
    position: fixed;
    top: 0;
    width: 100%;
    background: rgba(14, 13, 13, 0.37);
    -webkit-backdrop-filter: blur(8px);
            backdrop-filter: blur(8px);
}

nav ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
}

nav li {
    margin: 0 10px;
}

nav li a {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}

nav li a:hover {
    cursor: pointer;
    background-color: rgba(221, 221, 221, 0.185);
}

.resume-link {
    font-weight: bold;
}

.resume-link i {
    margin-left: 5px;
}
.about {
    height: 100vh; /* adjust this as needed */
    background: url(/HarnoorDhillonWebsite/static/media/stars.6a574ea4.jpeg) no-repeat center center;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .content-box {
    color: white;
    background: rgba(14, 13, 13, 0.37);  /* semi-transparent white */
    -webkit-backdrop-filter: blur(8px);
            backdrop-filter: blur(8px);  /* creates the blur effect */
    padding: 25px 100px 100px 100px;  /* top, right, bottom, left */
    border-radius: 30px;
    text-align: center;
    max-width: 80%; /* adjust as needed */
    margin: auto;
    align-items: flex-start;
  }
  .about h1 {
    font-family: 'Ubuntu', sans-serif; /* Change to the font you prefer */
    font-size: 70px; /* Adjust the size as needed */
  }
  .about p {
    font-family: 'Ubuntu', sans-serif; /* Change to the font you prefer */
    font-size: 20px; /* Adjust the size as needed */
  }
  .icon-container {
    display: flex;
    justify-content: center;  /* align the items in the center */
    padding: 20px;
  }
  
  .icon-container a {
    color: #fff; /* you can change this to your preferred color */
    margin: 0 10px; /* 0 top/bottom margin and 10px left/right margin */
    transition: transform 0.3s ease; /* this will animate the transform property */
  }
  
  .icon-container a:hover {
    transform: scale(1.2); /* this will enlarge the icon to 120% of its original size when hovered */
  }
  .scroll-down-icon {
    color: white;
    position: absolute;
    bottom: 50px;
    left: 50%;
    transform: translateX(-50%);
    transition: transform 0.3s ease-in-out;
    border-radius: 100%;
    cursor: pointer;
  }
  
  .scroll-down-icon:hover {
    transform: translateX(-50%) scale(1.2);
  }
.about-me {
    background-color: black;
    background-size: cover;
    height: 50vh;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}

.about-content {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    grid-gap: 200px;
    gap: 200px;
    padding: 0 20px; /* Add some horizontal padding to prevent content from touching the edge of the screen */
}

.face-pic {
    width: 200px;
    height: 200px;
    object-fit: cover;
    border-radius: 50%;
    flex-shrink: 0; /* Prevents the image from shrinking */
}

.about-details {
    display: flex;
    grid-gap: 100px;
    gap: 100px;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap; /* Allows the content to wrap onto the next line when there's not enough space */
}

/* This media query will take effect on screen widths up to 768px (typical for tablets and smaller) */
@media (max-width: 768px) {
    .about-content {
        flex-direction: column;
        align-items: center;
        grid-gap: 50px;
        gap: 50px;
    }

    .about-details {
        flex-direction: column;
        grid-gap: 50px;
        gap: 50px;
    }
}

.projects {
    background-color: black;
    color: white;
    padding: 20px;
    background-size: cover;
  }
  
  .projects h2 {
    margin-bottom: 70px;
    font-size: 32px; 
  }
  
  .project-columns {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  
  .project-box {
    width: 50%; /* adjust as needed */
    height: 100px; /* adjust as needed */
    background-color: white;
    margin: 0 auto 0px; /* center the box and add margin to the bottom */
    border-radius: 10px; /* add rounded corners */
    background-size: cover; /* Resize the background image to cover the entire container */
    background-position: center; /* Center the background image */
    position: relative; /* Position the .project-description relative to this */
    overflow: hidden; /* Hide any part of .project-description that's outside this */
}
  
  .project-box:hover .project-description {
    opacity: 1;
  }
  
  .project-description {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    transition: all 0.3s ease-in-out;
  }
#parallax {
    /* Adjust the height to fit your needs */
    height: 500px;
    background-image: url(/HarnoorDhillonWebsite/static/media/fern.553227c2.jpeg);
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
.contact-container {
    margin-top: 50px;
    padding: 20px;
    height: 50vh;
    color: white;
    text-align: center;
    background-size: cover;
}
.contact h2 {
  margin-bottom: 70px;
}

.contact-form-box {
    background-color: rgb(44, 41, 41);
    padding: 20px;
    border-radius: 10px;
}

.contact-form input[type='text'],
.contact-form input[type='email'],
.contact-form textarea {
    background-color: #333333;
    color: white;
    border: none;
    margin: 10px 0;
    width: 80%;
    padding: 10px;
    border-radius: 5px;
}

.contact-form input[type='text']::placeholder,
.contact-form input[type='email']::placeholder,
.contact-form textarea::placeholder {
    color: rgb(150, 143, 143);
}

.contact-form .message {
    height: 100px;
}

.form-btn {
  width: 200px; /* Adjust width as needed */
  color: #fff;
  background-color: #333;
  border: none;
  padding: 10px 15px;
  margin: 20px auto; /* Center button horizontally */
  cursor: pointer;
  font-size: 1em;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.form-btn:hover {
    background-color: #333;
}

.form-btn .react-icons {
    margin-left: 5px;
}

.project-cont {
    position: relative;
    z-index: 2000;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white; /* White text color */
    padding: 20px;
  }
  
  .project-desc {
    all: initial; /* Reset all inherited styles */
    display: block; /* Ensure it's not set to 'none' */
    color: white; /* Set a visible color */
    background-color: black; /* Temporary background for visibility */
    font-size: 16px; /* Set a visible font size */
    visibility: visible; /* Override any hidden visibility */
    opacity: 1; /* Ensure full opacity */
    padding-top: 0%;
    padding-left: 0%;
    max-width: 35%;
    font-size: 20px
  }
  .project-desc .slide5-link,
.project-desc .slide5-link:link,
.project-desc .slide5-link:visited,
.project-desc .slide5-link:hover,
.project-desc .slide5-link:active {
  color: white; /* Sets the link color to white */
  text-decoration: underline; /* Optional: for the classic hyperlink look */
}

  .project-img {
    flex: 1 1; /* Takes up half the container */
  }
  
  .project-img img {
    padding-top: 20%;
    padding-left: 10%;
    width: 100%; /* Full width of its container */
    height: auto; /* Maintain aspect ratio */
  }
  /* Button Styling */
.project-cont .back-button {
    all: initial;
    position: absolute;
    left: 0%; /* Adjust the value to move the button left; smaller values move it further left */
    top: 0%;
    background-color: #4CAF50; /* Green background */
    color: white; /* White text */
    padding: 10px 20px; /* Top/bottom and left/right padding */
    border: none; /* No border */
    border-radius: 5px; /* Rounded corners */
    cursor: pointer; /* Cursor changes to a pointer to indicate it's clickable */
    text-align: center; /* Center the text inside */
    text-decoration: none; /* No underline */
    display: inline-block; /* Allows setting padding and margins */
    font-size: 16px; /* Size of the text */
    margin: 4px 2px; /* Margin around the button */
    transition-duration: 0.4s; /* Smooth transition for hover effect */
  }
  
  /* Button Hover Effect */
  .project-cont .back-button:hover {
    background-color: white; /* White background */
    color: black; /* Black text */
  }
  
